.welcome-text-container {
  padding: 2rem 6rem;
  font-size: 63px;
  font-weight: 300;
  background-color: rgba(255, 255, 255, 0.45);
  box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 60%);
  border-radius: 6px;
  font-family: Inter;
  letter-spacing: 27%;
}

.bg-img {
  background: url(../../../assets/pexels-gabriel-peter-698379.jpg) no-repeat;
  background-position: 50%;
  background-size: cover;
  height: 81vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
